<template>
  <el-container>
    <el-header>
      <div class="title"><h1 style="color:#fff;line-height:28px;">
        <i class="el-icon-position"></i> 鑫家具电商管理系统
      </h1></div>
      <div class="admin"><span class="name">{{role==1?"超级管理员":"普通管理员"}}</span>
      <el-dropdown>
  <span class="el-dropdown-link">
    {{name}}<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
  <el-dropdown-menu slot="dropdown">
    <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>
      </div>
    </el-header>
    <el-container>
      <el-aside>
        <el-menu :default-active="active" :router="true" :default-openeds="opens">
          <el-submenu  v-for="item in rout" :key="item.name" :index="item.name">
            <template #title>
              <i :class="item.meta.icon"></i>
              <span>{{item.meta.name}}</span>
            </template>
            <el-menu-item v-for="me in item.children" :key="me.name" :index="me.path" :route="{name:me.name}">{{me.meta.name}}</el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      role: sessionStorage.getItem("role"),
      name: sessionStorage.getItem("name"),
      active:"password",
      // rout: this.$router.options.routes[1].children,
      rout:[],
    };
  },
  computed:{
    opens(){
      return this.rout.map(v=>v.name);
    }
  },
  methods: {
    logout() {
      sessionStorage.clear();
      this.$router.push({ name:"login"});
    }
  },
  mounted() {
    this.active = this.$route.name;
    // this.rout = this.$router.options.routes[1].children;
    if(sessionStorage.role==="1"){
      this.rout = this.$store.state.obj.children;
    }else {
      this.rout = this.$store.state.obj1.children;
    }
    console.log(this.rout)
  },
  beforeRouteUpdate(to,from,next){
    this.active = to.name;
    next();
  },
};
</script>

<style lang="scss" scoped>
.el-header {
  background-color: rgb(124, 124, 226);
}
.el-aside {
  background-color: rgb(59, 20, 20);
  height: calc(100vh - 60px);
}
.el-main {
  // background-color: #ccc;
}
.el-menu {
  height: 100%;
}
.title{
float: left;
}
.admin {
  width: 300px;
  height: 60px;
  line-height: 60px;
  float: right;
  color: #fff;
  span{
    padding-right: 30px;
    color: #fff;
  }
  .el-dropdown{
    margin-right: 30px;
  .el-dropdown-link {
    cursor: pointer;
    }
  }
}
</style>