import Vue from "vue";
import Vuex from "vuex";
import router from "@/router";

import Main from "@/views/Main"
// import User from "@/views/system/User"

// import Systems from "@/views/systems/Systems.vue" //h
/** import Shop from "@/views/goods/Shop" //h
import Order from "@/views/order/Order" // h
import Template from "@/views/dtemplate/Template" //h

import Password from "@/views/system/Password.vue" // h
import Manager from "@/views/system/Manager.vue" //h
import Banner from "@/views/goods/Banner" // h
import Type from "@/views/goods/Type" // h
import Goods from "@/views/goods/Goods" // h */


Vue.use(Vuex);

export default new Vuex.Store({
  state:{
    obj: {
      path: "/main",
      name: "main",
      component: Main,
      // redirect: {"name": "User"},
      children: [
        {
          path: "User",  //系统管理
          name: "User",
          meta:{
            name:"系统管理",
            icon:"el-icon-setting"
          },
          component: () => import(/* webpackChunkName: "user" */ '@/views/system/User'),
          children: [
            {
              path: "password",
              name: "password",
              meta:{
                name:"密码管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "user" */ '@/views/system/Password.vue'),
            },
            {
              path: "manager",
              name: "manager",
              meta:{
                name:"用户管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "user" */ '@/views/system/Manager.vue')
            },
            {
              path: "users",
              name: "users",
              meta:{
                name:"顾客管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "user" */ '@/views/system/Users.vue')
            },
          ]
        },
        {
          path: "shop",  // 商店
          name: "shop",
          component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Shop'),
          meta:{
            name:"商品管理",
            icon:"el-icon-goods"
          },
          children: [
            {
              path: "goods",
              name: "goods",
              meta:{
                name:"商品管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Goods')
            },
            {
              path: "type",
              name: "type",
              meta:{
                name:"分类管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Type')
            },
            {
              path: "banner",
              name: "banner",
              meta:{
                name:"轮播管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Banner')
            },
          ]
        },
        {
          path: "order",  // 订单管理
          name: "order",
          component: () => import(/* webpackChunkName: "order" */ '@/views/order/Order'),
          meta:{
            name:"订单管理",
            icon:"el-icon-files"
          },
          children: [
            {
              path: '/orders',
              name: 'orders',
              meta:{
                name:"订单管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "order" */ '@/views/order/Orders.vue')
            },
            {
              path: '/count',
              name: 'count',
              meta:{
                name:"统计管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "order" */'@/views/order/Count.vue')
            },
          ]
        },
        {
          path: "template",   // 配送模板管理
          name: "template",
          meta:{
            name:"模板管理",
            icon:"el-icon-connection"
          },
          component: () => import(/* webpackChunkName: "template" */'@/views/dtemplate/Template'),
          children: [
            {
              path: '/ddetail',
              name: 'ddetail',
              meta:{
                name:"详情管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "template" */ '@/views/dtemplate/Ddetail.vue')
            },
            {
              path: '/dtemplate',
              name: 'dtemplate',
              meta:{
                name:"模板管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "template" */ '@/views/dtemplate/Dtemplate.vue')
            },
          ]
        },
      ]
    },
    obj1: {
      path: "main",
      name: "main",
      component: Main,
      // redirect: {"name": "User"},
      children: [
        {
          path: "User",  //系统管理
          name: "User",
          meta:{
            name:"系统管理",
            icon:"el-icon-setting"
          },
          component: () => import(/* webpackChunkName: "user" */ '@/views/system/User'),
          children: [
            {
              path: "password",
              name: "password",
              meta:{
                name:"密码管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "user" */ '@/views/system/Password.vue'),
            },
          ]
        },
        {
          path: "shop",  // 商店
          name: "shop",
          component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Shop'),
          meta:{
            name:"商品管理",
            icon:"el-icon-goods"
          },
          children: [
            {
              path: "goods",
              name: "goods",
              meta:{
                name:"商品管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Goods')
            },
            {
              path: "type",
              name: "type",
              meta:{
                name:"分类管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Type')
            },
            {
              path: "banner",
              name: "banner",
              meta:{
                name:"轮播管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "shop" */ '@/views/goods/Banner')
            },
          ]
        },
        {
          path: "order",  // 订单管理
          name: "order",
          component: () => import(/* webpackChunkName: "order" */ '@/views/order/Order'),
          meta:{
            name:"订单管理",
            icon:"el-icon-files"
          },
          children: [
            {
              path: '/orders',
              name: 'orders',
              meta:{
                name:"订单管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "order" */ '@/views/order/Orders.vue')
            },
            {
              path: '/count',
              name: 'count',
              meta:{
                name:"统计管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "order" */'@/views/order/Count.vue')
            },
          ]
        },
        {
          path: "template",   // 配送模板管理
          name: "template",
          meta:{
            name:"模板管理",
            icon:"el-icon-connection"
          },
          component: () => import(/* webpackChunkName: "template" */'@/views/dtemplate/Template'),
          children: [
            {
              path: '/ddetail',
              name: 'ddetail',
              meta:{
                name:"详情管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "template" */ '@/views/dtemplate/Ddetail.vue')
            },
            {
              path: '/dtemplate',
              name: 'dtemplate',
              meta:{
                name:"模板管理",
                icon:"el-icon-setting"
              },
              component: () => import(/* webpackChunkName: "template" */ '@/views/dtemplate/Dtemplate.vue')
            },
          ]
        },
      ]
    },
  },
  mutations: {
    loadRoutes(state,payload){
      if(payload===1){
        router.addRoutes([state.obj])
      }else {
        router.addRoutes([state.obj1])
      }
    }
  },
})