// Vue.use(VueAxios, axios);

// 只要当前登录过的，在这里就可以调用vuex当中定义加载路由的方式
if (sessionStorage.getItem('role')) {
  let routes = JSON.parse(sessionStorage.getItem('role'))
  store.commit("loadRoutes", routes)
}


router.beforeEach(function(to,from,next){
  if (to.name!="login"&&!sessionStorage.getItem("name")){
    next({name:"login"});
  }else {
    next();
  }
})


new Vue({
  router,
  PuzzleVerification,
  store,
  render: h => h(App)
}).$mount('#app')
