<template>
  <div class="bg">
    <el-row type="flex" justify="center" align="middle">
      <el-col :span="8">
        <h1 style="margin-left:75px;padding-bottom:40px">家具电商后台管理系统</h1>
        <el-form
          :model="loginForm"
          label-width="80px"
          :rules="rules"
          ref="loginForm"
          :status-icon="true"
        >
          <el-form-item label="账  号" prop="username">
            <el-input v-model="loginForm.username" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item label="密  码" prop="password">
            <el-input v-model="loginForm.password" placeholder="请输入密码" type="password"></el-input>
          </el-form-item>
          <el-form-item style="text-align: center">
            <puzzle-verification
              v-model="isVerificationShow2"
              :puzzleImgList="puzzleImgList"
              blockType="puzzle"
              :onSuccess="handleSuccess"
              wraperPadding="40"
            />
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              style="width:100%"
              @click="handleLogin"
            >{{flag?"验证通过，请登录":"点击进行验证"}}</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <div class="copyright">Copyright ©2020 Berlin All Rights Reserved.</div>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      },
      rules: {
        username: { required: true, message: "请输入用户名", trigger: "blur" },
        password: { required: true, message: "请输入密码", trigger: "blur" }
      },
      // 是否显示
      isVerificationShow2: false,
      // 传入的图片
      puzzleImgList: [
        require("../assets/images/111.jpg"),
        require("../assets/images/222.jpg"),
        require("../assets/images/333.jpg"),
        require("../assets/images/444.jpg"),
        require("../assets/images/555.jpg")
      ],
      // 登录按钮的开关
      flag: false
    };
  },
  methods: {
    handleLogin() {
      this.$refs.loginForm.validate(v => {
        if (v) {
          // 验证是否通过登录 发生数据
          if (!this.flag) {
            this.isVerificationShow2 = true;
          } else {
            // 解决跨域 在浏览器端进行网络请求的时候 发出请求地址原则上必须和后端接口服务器的地址是同域名的(协议+域名+端口号)如果跨域默认不允许进行访问
            // 1.在服务器设置允许跨域
            // 2.使用jsonp的方式进行数据请求
            // 3.通过服务器端请求数据
            this.$http.post("/api/admin/login", this.loginForm).then(res => {
              if (res.data.code == 200) {
                // console.log(res);
                sessionStorage.setItem("name", res.data.username);
                sessionStorage.setItem("id", res.data.id);
                sessionStorage.setItem("role", res.data.role);
                sessionStorage.setItem("token",res.data.token);
                // 自定义路由
                this.$store.commit("loadRoutes", res.data.role);
                this.$router.push({
                  name: "goods"
                });
                this.$message.success(
                  "登录成功! 上次登录时间为:" + res.data.login_time
                );
              } else {
                this.$message.error("登录失败");
                this.flag = false;
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    // 滑块成功之后的操作
    handleSuccess() {
      this.isVerificationShow2 = false;
      this.flag = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.bg {
  width: 100%;
  height: 100%;
  background: url("../assets/loginBg.jpg") no-repeat center/cover;
}
.el-row {
  height: 100%;
}
.el-col {
  // height: 380px;
  background: rgba($color: #fff, $alpha: 0.3);
  border-radius: 50px;
  padding: 30px 10px;
  padding-right: 50px;
  h1 {
    text-align: center;
  }
}
.puzzle-container {
  text-align: center;
  // margin-left: 100px;
}
.copyright {
  width: 100%;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  text-align: center;
  position: absolute;
  bottom: 20px;
}
</style>